// breakpoints

$S: 320px;
$M: 768px;
$L: 1080px;
$XL: 1200px;

// media queries

@mixin MQ($canvas) {
  @if $canvas==S {
    @media only screen and (min-width: $S) and (max-width: $M) {
      @content;
    }
  }

  @else if $canvas==NS {
    @media only screen and (min-width: $M) {
      @content;
    }
  }

  @else if $canvas==M {
    @media only screen and (min-width: $M) and (max-width: $L) {
      @content;
    }
  }

  @else if $canvas==L {
    @media only screen and (min-width: $L) {
      @content;
    }
  }
}